@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap');
/* ======Contact page CSS======== */

.contact {
  letter-spacing: normal;
}


.contact .ContactBack {
  background-image: url('../../Assets/images/pic3.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 550px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.98;
  margin-bottom: 4rem;
}

.contact .ContactBack .contItem {
  padding: 2rem 4rem;
  margin: 0;
  margin-top: 5rem;
}

.contactForm {
  margin-bottom: 5rem;
}


.contactForm .btn {
  background-color: black;
  border: 2px solid#000000;
  color: #ffffff;
  border-radius: 10px;
  font-size: 20px;
  font-weight: 600;
  margin-top: 1rem;
  padding: 0.3rem 2rem;
}

.contactForm .btn:hover {
  color: #E1C343;
}

.contact h1 {
  font-size: 3.5rem;
  font-weight: 600;
  margin-bottom: 2rem;
  color: #ffffff;
  text-shadow: 0 2px 3px rgba(65, 61, 61, 0.5), 0 4px 7px rgba(0, 0, 0, .5), 0 9px 12px rgba(0, 0, 0, .5);
}

.contact .contItem {
  margin-bottom: 7rem;
}

.contact .officehour {
  margin: 1.3rem 0;
  font-size: 1.5rem;
  color: #e69300;
}

.contactInfo .heading h5 {
  color: #000000;
  font-size: 19px;
  font-weight: 600;
  margin: 0.2rem;
}

.contactInfo .heading p {
  color: #000000;
  font-size: 15px;
  margin-bottom: 1rem;
}

/* .socialIcons .iconss {
  padding: 1rem 0;
  font-size: 1.5rem;
} */

.contactForm .label {
  font-weight: 400;
  font-size: 18px;
  color: #000000;
}

.contactForm .label span {
  color: rgb(227, 5, 5);
}

.contactForm .input-field {
  border: none;
  border-radius: 0;
  height: 35px;
  background-color: #ECEFF0;
  ;
}

.contactForm .input-field option {
  background-color: #ECEFF0;
  color: #000000;
}

.contactForm .input-comment {
  border: none;
  border-radius: 0;
  padding: 10px;
  background-color: #ECEFF0;
}

.contactForm .checkbox {
  color: #000000;
  margin-right: 0.6rem;
}

.contactForm .input-field:focus {
  border-bottom: 3px solid #E1C343;
  border-radius: 0;
  box-shadow: none;
}

.contactForm .input-comment:focus {
  border-bottom: 3px solid #E1C343;
  box-shadow: none;
}

.formleft .socialMedia {
  font-size: 20px;
  letter-spacing: 0.5px;
  font-family: 'Poppins', sans-serif;
}


@media only screen and (max-width: 993px) {

  .contact .ContactBack {
    height: 350px;
  }

  /*
  .contactBy .Item h5 {
    font-size: 17px;
    font-weight: 600;
  } */
  .contactInfo {
    text-align: center;
    margin-bottom: 3rem;
  }
}

